@mixin lazyLoadingAnim {
    animation-name: lazyLoadingAnim;
    animation-duration: 0.5s;
    animation-fill-mode: forwards;
    animation-timing-function:ease-out;
    @keyframes lazyLoadingAnim {
        0% {
            transform: translateY(-150%);
            opacity: 0;
        }
        30% {
            opacity: 0;
        }
        100% {
            transform: translateY(0%);
            opacity: 1;
        }
    }
}

@mixin lazyLoadingAnim2 {
    animation-name: lazyLoadingAnim2;
    animation-duration: 0.5s;
    animation-fill-mode: forwards;
    animation-timing-function:linear;
    @keyframes lazyLoadingAnim2 {
        0% {
            opacity: 0;
        }
        100% {
            opacity: 1;
        }
    }
}